// App.jsx - Main application component
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { 
  ClerkProvider, 
  SignIn, 
  SignedIn, 
  SignedOut, 
  RedirectToSignIn,
  useUser
} from '@clerk/clerk-react';

// Firebase Configuration
import { db, storage } from './firebase';

// Components
import Header from './components/Header';
import Footer from './components/Footer';
import Home from './pages/Home';
import ReportForm from './pages/ReportForm';
import AdminDashboard from './pages/AdminDashboard';
import NotFound from './pages/NotFound';

// Replace with your actual Clerk publishable key
const clerkPubKey = process.env.REACT_APP_CLERK_PUBLISHABLE_KEY;

if (!clerkPubKey) {
  throw new Error('Missing Clerk Publishable Key');
}

// Protected route component
const ProtectedRoute = ({ children, adminOnly = false }) => {
  const { user, isLoaded } = useUser();
  
  if (!isLoaded) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }
  
  if (!user) return <RedirectToSignIn />;
  
  // Check email domain
  const emailDomain = user.primaryEmailAddress?.emailAddress.split('@')[1];
  if (emailDomain !== 'vnrvjiet.in') {
    return <Navigate to="/unauthorized" replace />;
  }
  
  // Check if admin route
  if (adminOnly) {
    const isAdmin = user.primaryEmailAddress?.emailAddress.includes('admin');
    if (!isAdmin) {
      return <Navigate to="/unauthorized" replace />;
    }
  }
  
  return children;
};

function App() {
  return (
    <ClerkProvider 
      publishableKey={clerkPubKey}
      allowedRedirectOrigins={['https://vnrvjiet-ragging-report.web.app']}
      signInUrl="/signin"
      signUpUrl="/signin"
    >
      <Router>
        <div className="flex flex-col min-h-screen bg-gray-50">
          <Header />
          <main className="flex-grow">
            <Routes>
              {/* Home Route */}
              <Route 
                path="/" 
                element={
                  <SignedIn>
                    <Home />
                  </SignedIn>
                } 
              />

              {/* Sign In Route */}
              <Route 
                path="/signin" 
                element={
                  <SignedOut>
                    <div className="flex justify-center items-center py-12">
                      <SignIn 
                        routing="path" 
                        path="/signin"
                        appearance={{
                          elements: {
                            rootBox: "mx-auto w-full max-w-md",
                            card: "bg-white shadow-md rounded-lg p-8",
                            headerTitle: "text-2xl font-bold text-blue-900",
                            headerSubtitle: "text-gray-600",
                            socialButtonsBlockButton: "bg-blue-700 hover:bg-blue-800"
                          }
                        }}
                      />
                    </div>
                  </SignedOut>
                } 
              />

              {/* Report Form Route */}
              <Route 
                path="/report" 
                element={
                  <ProtectedRoute>
                    <ReportForm />
                  </ProtectedRoute>
                } 
              />

              {/* Admin Dashboard Route */}
              <Route 
                path="/admin" 
                element={
                  <ProtectedRoute adminOnly={true}>
                    <AdminDashboard />
                  </ProtectedRoute>
                } 
              />

              {/* Unauthorized Access Route */}
              <Route 
                path="/unauthorized" 
                element={
                  <div className="text-center py-20">
                    <h2 className="text-2xl font-bold text-red-600">Unauthorized Access</h2>
                    <p className="mt-4">
                      You do not have permission to access this page.
                      Only VNRVJIET college emails are allowed.
                    </p>
                  </div>
                } 
              />

              {/* 404 Route */}
              <Route path="*" element={<NotFound />} />
            </Routes>
          </main>
          <Footer />
        </div>
      </Router>
    </ClerkProvider>
  );
}

export default App;